<template>
  <div class="reservations_main is-fullheight min-vh-100">
    <CottageOwnerNavbar v-if="role === 'ROLE_cottageOwner'"></CottageOwnerNavbar>
    <FishingInstructorNavbar v-if="role === 'ROLE_fishingInstructor'"></FishingInstructorNavbar>
    <BoatOwnerNavbar v-if="role === 'ROLE_boatOwner'" ></BoatOwnerNavbar>
    <ClientNavbar v-if="role === 'ROLE_client'"></ClientNavbar>
    <div class="container pt-5">
      <h1> Browse available entities </h1>
      <div class="search_div row">
        <div class="column col-2">
          <span style="color: white "  v-if="role !== 'ROLE_client'">Name</span>
          <input v-if="role !== 'ROLE_client'" class="input" ref="name"  type="text"  placeholder="Name"/>
          <span style="color: white "  v-if="role === 'ROLE_client'">Type</span>
          <select  v-if="role === 'ROLE_client'" ref="type" v-model="type" style="height: 40px; width:200px">
            <option>Cottage</option>
            <option>Boat</option>
            <option>Adventure</option>
          </select>
        </div>
        <div class="column col-2">
          <span style="color: white">Location</span>
          <select title="Select city" ref="location" style="height: 40px; width:200px">
            <option v-for="a in this.addresses" :value="a.city">
              {{a.city}}
            </option>
          </select>
        </div>
        <div class="column col-2">
          <span style="color: white">Price</span>
          <input class="input" ref="price" value="500" type="number" min="0" placeholder="Max price"/>
        </div>
        <div class="column col-2">
          <span style="color: white">People</span>
          <input class="input" type="number" value="4" placeholder="Number of people" ref="people" min="1" max="10" style="height: 40px">
        </div>
        <div class="column col-2">
          <span style="color: white">Sorting</span>
          <div class="select">
            <select title="Sorting" v-model="searchSort">
              <option selected="selected" value="NO_SORT" >No sorting</option>
              <option value="PRICE_ASC">Price: Ascending</option>
              <option value="PRICE_DESC">Price: Descending</option>
              <option value="RATING_ASC">Rating: Ascending</option>
              <option value="RATING_DESC">Rating: Descending</option>
            </select>
          </div>
        </div>
      </div>
      <div class="columns">
        <div class="column col-2 ">
          <span style="color: white">Start date</span>
          <input type="date" name="startDate" ref="startDate" value="2022-07-06" style="height: 40px; width:200px">
        </div>
        <div class="column col-2 ">
          <span style="color: white">Start time</span>
          <input type="time" name="startTime" ref="startTime" value="10:00:00" style="height: 40px; width:200px">
        </div>

        <div class="column col-2">
          <span style="color: white">End date</span>
          <input type="date" name="endDate" ref="endDate" value="2022-07-10" style="height: 40px; width:200px">
        </div>
        <div class="column col-2">
          <span style="color: white">End time</span>
          <input type="time" name="endTime" ref="endTime"value="11:00:00"  style="height: 40px; width:200px">
        </div>


        <div class="column col-2">
          <span style="color: white">Rating</span>
          <star-rating :rating="3" :read-only="false" :increment="1" :show-rating="false"  @rating-selected="setRating" :star-size="35" :size="300" style="height: 40px" ></star-rating>
        </div>
        <div class="column col-2">
          <button v-if="role!=='ROLE_client' || client.penalties < 3" type="submit" v-on:click="search()" class="btn search_btn" style="float: left">Search</button>
          <button v-else class="disabled btn" style="background-color: grey">Search</button>
        </div>
      </div>

      <div v-if="client.penalties >= 3">
        <p style="color: white"> You already have 3 penalties, so you can't make any reservations this month. Come back next month.</p>
      </div>

      <div v-else>
        <div v-if="entities.length === 0">
          <p style="color: white"> Nothing to show for now.</p>
        </div>

        <div v-for="e in entities">
        <CottageBrowseCard v-if="e.type === 'Cottage'" :cottage="e" :canReserve="true"  v-on:reserve="reserve($event)"></CottageBrowseCard>
        <AdventureBrowseCard v-else-if="e.type === 'Adventure'" :adventure="e" :canReserve="true"  v-on:reserve="reserve($event)"></AdventureBrowseCard>
          <BoatBrowseCard v-else :boat="e" :canReserve="true"  v-on:reserve="reserve($event)"></BoatBrowseCard>

        </div>
      </div>

    </div>

  </div>
</template>

<script>
import ClientNavbar from "./header/ClientNavbar";
import axios from "axios";
import CottageBrowseCard from "./CottageBrowseCard";
import CottageOwnerNavbar from "./header/CottageOwnerNavbar";
import FishingInstructorNavbar from "./header/FishingInstructorNavbar";
import router from "../router";
import AdventureBrowseCard from "./AdventureBrowseCard";
import BoatOwnerNavbar from "./header/BoatOwnerNavbar";
import BoatBrowseCard from "./BoatBrowseCard";
import _orderBy from 'lodash/orderBy';

export default {
  name: "Reservations",
  components: {
    BoatBrowseCard,
    CottageBrowseCard,
    BoatOwnerNavbar,FishingInstructorNavbar, CottageOwnerNavbar, ClientNavbar, AdventureBrowseCard},
  data: function(){
    return{
      addresses: '',
      entities: '',
      params: '',
      type: '',
      rating: 3,
      role:'',
      clientId: this.$route.params.clientId,
      client: '',
      searchSort: "NO_SORT"
    }
  },
  mounted: function () {
    this.role = sessionStorage.getItem("role");
    axios
      .get("https://rental-app-6.herokuapp.com"+"/api/address/all")
      .then(response => (this.addresses = response.data));

    if (this.role === "ROLE_client") {
      axios
        .get("https://rental-app-6.herokuapp.com" + "/api/clients/getClient", {
          headers: {
            Authorization:
              'Bearer ' + sessionStorage.getItem("accessToken")
          }
        })
        .then(response => {
          this.client = response.data
        });
    }

    this.type = "Cottage";
  },
  methods: {
    search: function (){
      if(this.$refs.startDate.value >= this.$refs.endDate.value){
        if(this.$refs.startDate.value === this.$refs.endDate.value){
          if(this.$refs.startTime.value >= this.$refs.endTime.value){
            this.show('foo-css', 'error',`<p style="font-size: 25px">Invalid dates</p>`,`<p style="font-size: 20px">Start date and time must be before the end.</p>`)
            return
          }
        } else{
          this.show('foo-css', 'error',`<p style="font-size: 25px">Invalid dates</p>`,`<p style="font-size: 20px">Start date must be before the end date.</p>`)
          return
        }
      }
      if(this.role === 'ROLE_client'){
        this.params = {type: this.$refs.type.value, city: this.$refs.location.value, price: this.$refs.price.value, people: this.$refs.people.value,
          startDate: this.$refs.startDate.value, startTime: this.$refs.startTime.value, endDate: this.$refs.endDate.value, endTime: this.$refs.endTime.value, rating: this.rating};
        axios
          .post("https://rental-app-6.herokuapp.com"+"/api/entity/getAvailable", this.params,{
            headers: {
              Authorization:
                'Bearer ' + sessionStorage.getItem("accessToken")
            }})
          .then(response => {
            this.entities = response.data
            if(this.searchSort !== "NO_SORT"){
              if(this.searchSort === "PRICE_ASC"){
                this.entities = _orderBy(this.entities, 'price', 'asc')
              }

              else if(this.searchSort === "PRICE_DESC"){
                this.entities = _orderBy(this.entities, 'price', 'desc')
              }

              else if(this.searchSort === "RATING_ASC"){
                this.entities = _orderBy(this.entities, 'averageGrade', 'asc')
              }

              else if(this.searchSort === "RATING_DESC"){
                this.entities = _orderBy(this.entities, 'averageGrade', 'desc')
              }
            }

          }).catch(function error(error) {
          alert(error.response.data);});
      }else if(this.role === 'ROLE_cottageOwner'){
        this.params = {name: this.$refs.name.value, city: this.$refs.location.value, price: this.$refs.price.value, people: this.$refs.people.value,
          startDate: this.$refs.startDate.value, startTime: this.$refs.startTime.value, endDate: this.$refs.endDate.value, endTime: this.$refs.endTime.value, rating: this.rating};
        axios
          .post("https://rental-app-6.herokuapp.com"+"/api/cottages/getAvailable", this.params,{
            headers: {
              Authorization:
                'Bearer ' + sessionStorage.getItem("accessToken")
            }})
          .then(response => (this.entities = response.data)).catch(function error(error) {
          alert(error.response.data);});
      }else if(this.role === 'ROLE_boatOwner'){
        this.params = {name: this.$refs.name.value, city: this.$refs.location.value, price: this.$refs.price.value, people: this.$refs.people.value,
          startDate: this.$refs.startDate.value, startTime: this.$refs.startTime.value, endDate: this.$refs.endDate.value, endTime: this.$refs.endTime.value, rating: this.rating};
        axios
          .post("https://rental-app-6.herokuapp.com"+"/api/boats/getAvailable", this.params,{
            headers: {
              Authorization:
                'Bearer ' + sessionStorage.getItem("accessToken")
            }})
          .then(response => (this.entities = response.data)).catch(function error(error) {
          alert(error.response.data);});
      }else if(this.role === 'ROLE_fishingInstructor'){
        this.params = {name: this.$refs.name.value, city: this.$refs.location.value, price: this.$refs.price.value, people: this.$refs.people.value,
          startDate: this.$refs.startDate.value, startTime: this.$refs.startTime.value, endDate: this.$refs.endDate.value, endTime: this.$refs.endTime.value, rating: this.rating};
        axios
          .post("https://rental-app-6.herokuapp.com"+"/api/adventures/getAvailable", this.params,{
            headers: {
              Authorization:
                'Bearer ' + sessionStorage.getItem("accessToken")
            }})
          .then(response => (this.entities = response.data)).catch(function error(error) {
          alert(error.response.data);});
      }
    },
    show: function (group, type = '',title, text) {
      this.$notify({group, title, text, type})
    },

    setRating: function (rating){
      this.rating = rating;
    },
    reserve: function (entity){
      let entitytype = ''
      if(entity.type === "Cottage" || entity.type === "Adventure"){
        entitytype = entity.type
      } else{
        entitytype = "Boat"
      }
      var data = {personNum: this.$refs.people.value, startDate: this.$refs.startDate.value, startTime: this.$refs.startTime.value,
        endDate: this.$refs.endDate.value, endTime: this.$refs.endTime.value, entityId: entity.id, clientId:this.clientId, rating: this.rating, type: entitytype}
      axios
        .post("https://rental-app-6.herokuapp.com"+"/api/reservation/reserve", data,{
          headers: {
            Authorization:
              'Bearer ' + sessionStorage.getItem("accessToken")
          }})
        .then(response => { this.show('foo-css', 'success',`<p style="font-size: 25px">Reserved</p>`,`<p style="font-size: 20px">Successfully reserved</p>`)
          setTimeout(() => {
          if(this.role === 'ROLE_client'){
          location.reload(); }else{
            router.push('/currentReservations');
          }

          }, 1500) }).catch(error => {
                if(error.response.status === 400){
                  this.show('foo-css', 'error',`<p style="font-size: 25px">Reservation failed</p>`,`<p style="font-size: 17px">You can't reserve at the same time if you have canceled already.</p>`)
                } else if(error.response.status === 404){
                  this.show('foo-css', 'error',`<p style="font-size: 25px">Reservation failed</p>`,`<p style="font-size: 17px">You already have a reservation in this period or someone else has just reserved.</p>`)
                }else if(error.response.status === 409){
                  this.show('foo-css', 'error',`<p style="font-size: 25px">Conflict</p>`,`<p style="font-size: 17px">Someone else has just reserved in this period.</p>`)
                }
                else{
                  alert(error.response.data);
                }

          });
    }
  }
}
</script>

<style scoped>
.reservations_main {
  background-attachment: fixed;
  background-image: url('../assets/images/back2.jpg');
  background-position-y: 0;
  background-repeat: no-repeat;
  background-size: cover;
  height: fit-content;
  opacity: 1;
  position: absolute;
  top: 0;
  transition: opacity 0.3s linear 0s;
  width: 100%;
  overflow: visible;
}
h1{
  padding-bottom: 30px;
  color: white;
}
button {
  box-shadow: none;
  border: none;
  background-color: #2e6b6b;
  cursor: pointer;
  text-transform: uppercase;
  color: white;
  margin-top: 25px;
  height: 40px;
}
button:hover {
  background: darkcyan;
}
button:focus {
  background: darkcyan;
  box-shadow: none
}
button:active {
  background: darkcyan;
  box-shadow: none
}
a{
  text-decoration: none;
}
star-rating{
  align-self: normal;
}
</style>



